<template>
  <!--     -->

  <h1>  </h1>
  <h1>  </h1>

  <div class="container">
    <h1>{{ isEditing ? " " : " " }}</h1>
    <form class="post-form" @submit.prevent="submitPost">
      <input v-model="title" type="text" placeholder="" class="input" required />
      <textarea v-model="content" placeholder="" class="textarea" required></textarea>
      <div>
        <label for="image-upload">:</label>
        <input id="image-upload" type="file" @change="handleFileChange" />
        <img v-if="imagePreview" :src="imagePreview" alt="" width="200" />
      </div>
      <button type="submit" class="button">
        {{ isEditing ? "" : "" }}
      </button>
    </form>
  </div>

  <!-- footer    -->

  <!-- footer   -->

  <router-view />
</template>

<script setup>
import { ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";
import { useUserStore } from "@/store/SignUp";
import { storeToRefs } from "pinia";

const route = useRoute();
const router = useRouter();
const userStore = useUserStore();
const { isLoggedIn } = storeToRefs(userStore);

const title = ref("");
const content = ref("");
const image = ref(null);
const imagePreview = ref(null);
const isEditing = ref(false);

const handleFileChange = (event) => {
  const file = event.target.files[0];
  if (file) {
    image.value = file;
    const reader = new FileReader();
    reader.onload = (e) => {
      imagePreview.value = e.target.result;
    };
    reader.readAsDataURL(file);
  }
};

const fetchPost = async (id) => {
  try {
    const response = await axios.get(`/employmentboard/${id}`);
    title.value = response.data.title;
    content.value = response.data.content;
    //    (  URL  )
    // imagePreview.value = response.data.imageURL;
  } catch (error) {
    console.error("Error fetching post:", error);
  }
};

const submitPost = async () => {
  if (!isLoggedIn.value) {
    alert(" .");
    router.push({ name: "Login" });
    return;
  }

  if (!title.value || !content.value || !userStore.id) {
    console.error("Missing required fields: title, content, or userId");
    alert(", ,   ID .");
    return;
  }

  const formData = new FormData();
  formData.append("title", title.value);
  formData.append("content", content.value);
  formData.append("userId", userStore.id);
  if (image.value) {
    formData.append("image", image.value);
  }

  try {
    if (isEditing.value) {
      await axios.put(`/employmentboard/${route.params.id}`, formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
    } else {
      await axios.post("/employmentboard", formData, {
        headers: { "Content-Type": "multipart/form-data" },
      });
    }
    router.push({ name: "Employment" });
  } catch (error) {
    console.error(
      "Error submitting post:",
      error.response ? error.response.data : error.message
    );
    alert(" /   .");
  }
};

onMounted(() => {
  if (route.params.id) {
    isEditing.value = true;
    fetchPost(route.params.id);
  }
});
</script>
