<template>
  <div class="project-container">
    <h1 class="project-title"> </h1>
    <div class="nav-buttons">
      <router-link class="nav-button active" :to="{ name: 'RecommendationProposal' }"> </router-link>
      <router-link class="nav-button" :to="{ name: 'ActivityPlan' }"> </router-link>
      <router-link class="nav-button" :to="{ name: 'Presentation' }"> </router-link>
      <router-link class="nav-button" :to="{ name: 'FinalReport' }"> </router-link>
      <router-link class="nav-button" :to="{ name: 'Code' }"></router-link>
    </div>
  </div>

<div class="edit-container">
  <form @submit.prevent="handleSubmit">re1 
    <div class="form-group">
      <label for="title">:</label>
      <input
        v-model="post.title"
        type="text"
        id="title"
        class="form-control"
        required
      />
    </div>
    <div class="form-group">
      <label for="content">:</label>
      <textarea
        v-model="post.content"
        id="content"
        class="form-control"
        rows="10"
        required
      ></textarea>
    </div>
    <div class="form-group">
      <label for="image">:</label>
      <input
        type="file"
        id="image"
        class="form-control"
        @change="handleImageChange"
      />
      <div v-if="post.image">
        <img
          :src="`/uploads/${post.image}`"
          alt=" "
          class="post-image"
        />
        <button type="button" @click="removeImage"> </button>
      </div>
    </div>
    <button type="submit" class="btn btn-primary"></button>
  </form>
</div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import axios from 'axios';

const route = useRoute();
const router = useRouter();

const post = ref({
title: '',
content: '',
image: ''
});

const selectedImage = ref(null);

const fetchPost = async (id) => {
try {
  const response = await axios.get(`/proposal/${id}`);
  post.value = response.data;
} catch (error) {
  console.error('Error fetching post:', error);
}
};

const handleImageChange = (event) => {
selectedImage.value = event.target.files[0];
};

const removeImage = () => {
post.value.image = null;
};

const handleSubmit = async () => {
const formData = new FormData();
formData.append('title', post.value.title);
formData.append('content', post.value.content);
formData.append('image', selectedImage.value);
formData.append('removeImage', post.value.image ? 'false' : 'true');

try {
  await axios.put(`/proposal/${route.params.id}`, formData, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  });
  alert(' .');
  router.push({ name: 'DetRecommendationProposal', params: { id: route.params.id } });
} catch (error) {
  console.error('Error updating post:', error);
}
};

onMounted(() => {
if (route.params.id) {
  fetchPost(route.params.id);
}
});
</script>

<style scoped>
/*   */
.post-image {
width: 100%;
max-width: 600px;
height: auto;
margin-top: 10px;
}
</style>
