import { ref, computed, onMounted } from 'vue';
import axios from 'axios';
import { useRoute, useRouter } from 'vue-router';
import { useUserStore } from '@/store/SignUp';
import { storeToRefs } from 'pinia';

export const useRecruitmentNoticeDetail = () => {
  const route = useRoute();
  const router = useRouter();
  const userStore = useUserStore();
  const { isLoggedIn, id: userId } = storeToRefs(userStore);

  const post = ref({
    title: '',
    content: '',
    id: 0,
    userId: 0,
    image: ''  //   
  });
  const comments = ref([]);
  const comment = ref('');
  const isAuthor = computed(() => isLoggedIn.value && post.value.userId === userId.value);

  const fetchPostDetails = async () => {
    try {
      const response = await axios.get(`http://localhost:3000/posts/${route.params.id}`);
      post.value = response.data;
      console.log('Post Data:', post.value); //   

      const commentsResponse = await axios.get(`http://localhost:3000/posts/${route.params.id}/comments`);
      console.log('Comments Data:', commentsResponse.data);
      comments.value = commentsResponse.data;
    } catch (error) {
      console.error('Error fetching post details:', error);
    }
  };

  const editPost = () => {
    router.push({ name: 'ModPrRecruitmentNotice', params: { id: route.params.id } });
  };

  const deletePost = async () => {
    if (!confirm('   ?')) return;

    try {
      await axios.delete(`http://localhost:3000/posts/${post.value.id}`);
      router.push({ name: 'RecruitmentNotice' }); //    
    } catch (error) {
      console.error('Error deleting post:', error);
      alert('    .');
    }
  };

  const submitComment = async () => {
    if (!comment.value.trim()) {
      alert('   .');
      return;
    }

    try {
      await axios.post(`http://localhost:3000/posts/${post.value.id}/comments`, {
        content: comment.value,
        authorId: userId.value  //   ID
      });
      comment.value = ''; //    
      fetchPostDetails(); //   
    } catch (error) {
      console.error('Error submitting comment:', error);
      alert('    .');
    }
  };

  const deleteComment = async (commentId) => {
    try {
      await axios.delete(`http://localhost:3000/comments/${commentId}`);
      fetchPostDetails(); //   
    } catch (error) {
      console.error('Error deleting comment:', error);
      alert('    .');
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
  };

  const logout = () => {
    userStore.isLoggedIn = false;
    userStore.isAdmin = false;
    router.push({ name: 'Home' });
  };

  //   
  onMounted(() => {
    fetchPostDetails();
  });

  return {
    post,
    comments,
    comment,
    isAuthor,
    editPost,
    deletePost,
    submitComment,
    deleteComment,
    formatDate,
    logout
  };
};
