export default {
  data() {
    return {
      firstSectionVisible: false,
      sectionGroups: [
        `<strong></strong> 2021 11 29 ,
                .
              1,362.04( 412),       .`,
        ` <strong>7 </strong>  ,
                  .
           , 3D  IT       ,
          <strong>      </strong>  .
          , ,  , , ,      
               .`,
        `<strong>   </strong>    248cm(98) , 3D ,  ,
                 .
                IT       !`
      ],
      groupVisible: [false, false, false],
      images: [
        { title: " 1", filename: "image/Main.png" },
        { title: " 2", filename: "image/CenterLobby.png" },
        { title: " 3", filename: "image/CenterMain.png" },
        { title: " 4", filename: "image/tr1.png" },
        { title: " 5", filename: "image/tr2.png" },
        { title: " 6", filename: "image/tr3.png" }
      ],
      directions: [
        "image/.png"
      ],

      directionss: [
        "image/.png"
      ],

      duplicatedImages: [], //      
      autoSlideTimer: null, //      
      scrollAmount: 200, //     
      isDown: false,
      startX: 0,
      scrollLeft: 0
    };
  },
  mounted() {
    setTimeout(() => {
      this.firstSectionVisible = true;
    }, 500);

    this.sectionGroups.forEach((group, index) => {
      setTimeout(() => {
        this.groupVisible[index] = true;
      }, 1000 + index * 1500);
    });

    this.setupCarousel(); //   
    this.startAutoSlide(); //   

    //   
    window.addEventListener('mouseup', this.endDrag);
    window.addEventListener('mousemove', this.onDrag);
  },
  beforeDestroy() {
    //   
    window.removeEventListener('mouseup', this.endDrag);
    window.removeEventListener('mousemove', this.onDrag);
    this.stopAutoSlide(); //    
  },
  methods: {
    setupCarousel() {
      //        
      this.duplicatedImages = [...this.images, ...this.images];
      
      //       
      this.$nextTick(() => {
        const carousel = this.$refs.carousel;
        if (carousel) {
          carousel.scrollLeft = carousel.scrollWidth / 2;
        }
      });
    },
    startAutoSlide() {
      if (!this.autoSlideTimer) { //       
        this.autoSlideTimer = setInterval(() => {
          this.autoSlide();
        }, 20); //    
      }
    },
    stopAutoSlide() {
      if (this.autoSlideTimer) {
        clearInterval(this.autoSlideTimer); //  
        this.autoSlideTimer = null; //   
      }
    },
    autoSlide() {
      const carousel = this.$refs.carousel;
      if (carousel) {
        const maxScrollLeft = carousel.scrollWidth / 2; //   

        if (carousel.scrollLeft >= maxScrollLeft) {
          carousel.scrollLeft = 0; //  
        } else {
          carousel.scrollLeft += 1; //  
        }
      }
    },
    startDrag(e) {
      this.isDown = true;
      this.startX = e.pageX - e.currentTarget.offsetLeft;
      this.scrollLeft = e.currentTarget.scrollLeft;
      this.stopAutoSlide(); //     
    },
    endDrag() {
      this.isDown = false;
      this.startAutoSlide(); //      
    },
    onDrag(e) {
      if (!this.isDown) return;
      e.preventDefault();
      const x = e.pageX - e.currentTarget.offsetLeft;
      const walk = (x - this.startX) * 1; //     (: 1 )
      e.currentTarget.scrollLeft = this.scrollLeft - walk;
    },
    getSectionStyle(groupIndex) {
      const colors = ['#f5a623', '#b7e4a1', '#a2d9ff']; //  , , 
      const borderRadius = '30px'; //  border-radius 

      return {
        'background-color': colors[groupIndex],
        'border-radius': borderRadius
      };
    }
  }
};
